.card {
  box-sizing: border-box;
  background: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-wrap: wrap;
  justify-content: center;
  height: 408px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__content {
  display: block;
}

.card__image {
  background-image: url("imac.202c52ff.jpeg");
  background-repeat: no-repeat;
  background-size: contain;
  width: 100%;
  height: 134px;
  margin-inline: auto;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  justify-self: start;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviewers {
  color: #060b35;
  text-align: right;
  flex-direction: column;
  justify-content: flex-end;
  width: 53px;
  height: 16px;
  font-size: 10px;
  line-height: 14px;
  display: flex;
}

.card__rate {
  justify-content: space-between;
  margin-top: 16px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  margin-top: 24px;
  display: flex;
}

.card__price-text {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

.card__price-number {
  color: #060b35;
  text-align: right;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  margin-top: 16px;
}

.stars {
  gap: 8px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-size: cover;
  width: 12px;
  height: 12px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.button {
  color: #fff;
  text-transform: uppercase;
  background: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  display: flex;
}

.button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

